@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif !important;
}
body {
  overflow-x: hidden;
}
html {
  scroll-behavior: smooth;
}
.main-navbar {
  font-family: Poppins, sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
  z-index: 100;
  transition: all 1s ease-in-out;
  background-color: transparent;
}

.navbar-shadow {
  -webkit-box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  -moz-box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  background-color: white;
}

.navbar-logo {
  cursor: pointer;
  font-size: 24px;
  font-weight: 600;
  color: #333;
  .navbar-logo-first-element {
    color: #003366;
  }
  .navbar-logo-second-element {
    color: #ffa500;
  }
}

.navbar-menu {
  display: flex;
  gap: 20px;
  padding-right: 30px;
  font-weight: 500;
  font-size: 18px;
}

.navbar-menu-item {
  text-decoration: none;
  color: #ffa500;
  cursor: pointer;
  transition: color 0.4s ease-in-out;
}

.navbar-menu-item:hover {
  color: #666;
}

.hero {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  position: relative;
  background-image: url('../../images/malaga-hero.png');
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  .hero-menu {
    width: 100%;
    margin-top: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;

    .hero-menu-item {
      font-size: 24px;
      font-weight: 600;
      color: #ffa500;
      text-decoration: none;
      z-index: 2;
      position: relative;
      transition: color 0.4s ease-in-out;
    }
    .hero-menu-item:hover {
      cursor: pointer;
      color: #ffffff;
    }
  }
  .main-content {
    width: 100%;
    height: 50%;

    #hero-first-element {
      font-size: 50px;
      font-weight: 700;
      color: #ffa500;
      margin-bottom: 20px;
      z-index: 2;
      position: relative;
      font-family: 'Poppins', sans-serif;
    }
    #hero-second-element {
      font-size: 30px;
      font-weight: 500;
      color: #ffa500;
      z-index: 2;
      position: relative;
      font-family: 'Poppins', sans-serif;
    }
  }
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 51, 102, 0.5);
  z-index: 1;
}

.about {
  width: 100%;
}

.description {
  display: flex;
  flex-flow: column nowrap;
  align-items: start;
  justify-content: space-between;
  padding: 0 10% 0 10%;
  margin: 100px 0;
}
.description-title-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  hr {
    width: 50%;
    border: none;
    border-bottom: 2px solid #ffa500;
    outline: none;
  }
}
.description-title {
  display: block;
  margin: 20px 0;
  color: #333;
  font-size: 28px;
  font-weight: 600;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}

.description-text-image {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  gap: 10%;
  margin-top: 100px;
  &.reverse {
    flex-flow: row-reverse nowrap;
  }
  .description-text-wrapper {
    width: 100%;
  }
  & .description-text {
    width: 100%;
    color: rgb(86, 86, 86);
    font-size: 20px;
    font-weight: bold;
    text-align: left;
  }
}

.description-image {
  border-radius: 50px;
  width: 35vw;
  height: 20vw;
  object-fit: cover;

  -webkit-box-shadow: 0px 11px 22px -2px rgba(66, 68, 90, 1);
  -moz-box-shadow: 0px 11px 22px -2px rgb(47, 48, 57);
  box-shadow: 0px 11px 22px -2px rgba(66, 68, 90, 1);
}

.expectations {
  margin: 30vh 0;
  padding: 0;

  .description-title-wrapper {
    margin-left: 1vw;
    margin-bottom: 100px;
  }

  .description-content {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    z-index: 0;
    overflow: hidden;
    width: 100%;

    .expectations-control {
      z-index: 1;
      width: 100%;
      position: absolute;
      align-self: center;
      justify-self: unset;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      padding: 0 1vw;

      .left-arrow,
      .right-arrow {
        width: 3vw;
        fill: #003366;
      }
    }

    .expectations-list {
      padding: 2vh 1vw;
      display: flex;
      position: relative;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      transition: left 1s;
    }
  }
}

.process {
  margin: 30vh 0;
  padding: 0;

  .description-title-wrapper {
    margin-bottom: 100px;
  }
  .description-title {
    margin-left: 1vw;
  }

  .description-content {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    padding: 0 3vw;

    table {
      border: 1px solid #003366;
      border-collapse: collapse;
      width: 80vw;

      margin: auto;

      thead {
        tr {
          background-color: #003366;
          th {
            padding: 1vh 1vw;
            color: white;
            text-align: center;
          }
        }
      }

      tbody {
        tr {
          border: none;
          th,
          td {
            padding: 1vh 1vw;

            &:nth-of-type(4) {
              display: flex;
              flex-flow: row nowrap;
              align-items: center;
              justify-content: center;
            }
          }

          &:nth-child(even) {
            background-color: #e1e1e1;
          }
          &:nth-child(odd) {
            background-color: white;
          }
        }
      }

      tr:nth-child(even) {
        background-color: #e1e1e1;
      }
    }


    .link-button {
      border: 1px solid #003366;
      padding: 1vh 1vw;
      width: fit-content;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      text-decoration: none;
      text-decoration-line: none;
      background-color: #003366;
      transition: background-color 0.2s;

      .label {
        color: white;
        margin-right: 1vw;
        font-size: 12px;

        &.no-external {
          margin-right: 0;
        }
      }

      .icon {
        fill: white;
        height: 12px;
      }

      &:hover {
        background-image: none;
        background-color: #0066cc;

        .label {
          font-weight: 500;
        }

        .icon {
          font-weight: 500;
        }
      }
    }
  }
}

.attendees {
  margin: 30vh 0;
  padding: 0;

  .description-title-wrapper {
    margin-bottom: 100px;
  }
  .description-title {
    margin-left: 1vw;

    padding-bottom: 1vh;

    a {
      position: relative;
      top: 0px;
      margin-left: 1vw;
      padding: 0 0.5vw;
      transition: box-shadow 0.2s, top 0.2s;

      &:hover {
        top: -1px;
        box-shadow: 0px 0px 5px #003366;
      }

      &:active {
        top: 1px;
        box-shadow: none;
      }

      .download {
        height: 16px;
        fill: #ffa500;
      }
    }
  }

  .description-content {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    padding: 0 3vw;

    table {
      border: 1px solid #003366;
      border-collapse: collapse;
      width: 80vw;

      margin: auto;

      thead {
        tr {
          background-color: #003366;
          th {
            padding: 1vh 1vw;
            color: white;
            text-align: center;
          }
        }
      }

      tbody {
        tr {
          border: none;
          th,
          td {
            padding: 1vh 1vw;
          }

          &:nth-child(even) {
            background-color: #e1e1e1;
          }
          &:nth-child(odd) {
            background-color: white;
          }
        }
      }
    }
  }
}

.opinions {
  margin: 30vh 0;
  padding: 0;

  .description-title-wrapper {
    margin-left: 1vw;
    margin-bottom: 100px;
  }

  .description-content {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    z-index: 0;
    overflow: hidden;
    width: 100%;

    .opinions-list {
      padding: 2vh 1vw;
      display: flex;
      position: relative;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      transition: left 1s;
    }
  }
}

footer {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: space-evenly;
  background-image: linear-gradient(to top right, #003366 60%, #0066cc);
  padding: 2vh 0;

  .project-details-container {
    width: 100%;
    display: flex;
    flex-flow: column-reverse nowrap;
    align-items: center;
    justify-content: space-between;

    .project-details {
      color: white;
      margin-top: 4vh;
      font-family: 'Poppins', sans-serif;
    }

    .partners {
      width: 100%;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-evenly;

      img {
        height: 12vh;
      }
    }
  }

  .authors-social-media {
    margin-top: 4vh;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;

    .authors {
      height: 5vh;
      color: white;

      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-evenly;
      flex-grow: 1;
      font-size: 12px;
    }

    .social-media {
      width: 10vw;
      height: 5vh;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      margin-right: 2vw;

      .link {
        margin: 0 0.5vw;
        .fb,
        .youtube {
          height: 5vh;
          fill: white;
        }
      }
    }
  }
}
.show-hide-navbar {
  display: none;
}
